.banner{
    background-size: cover;
    background-position: center center;
    color:white;
    object-fit: contain;
    height: 448px;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    width:100%;
}

.banner_contents{
    padding-left: 30px;
    padding-top: 140px; 
    height: 190px;
    text-align: left;
}

.banner_title {
    font-size: 3rem;
    font-weight:  700;
    padding-bottom: 15px;
}

.banner_description{
    width:45rem;
    line-height: 1.4;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
    font-weight: 500;
}

.banner_button {
    cursor:pointer;
    color:#fff;
    outline: none;
    border: none;
    font-weight: 600;
    border-radius: 4px;
    padding: 0.5rem 2rem;
    margin-right: 1rem;
    background-color: rgba(51,51,51,0.5);
}

.banner_button:hover{
    color:rgb(0, 0, 0);
    background-color: #e6e6e6;
    transition: all 0.2s;
}

.banner_fadeBottom{
    /* margin-bottom:10px ; */
   height:7.4rem;
   background-image: linear-gradient(
       180deg,
       transparent,
       rgba(37,37, 37, 0.61),
       #111
   ); 
}